.header {
  background-color: #FFF;
  padding: 20px;
  .header-icon {
    background-color: #F9F9F9;
    padding: 10px;
    border-radius: 50%;
    color: #555;
    cursor: pointer;
    margin-left: 10px;
  }
  .toggle-sidebar {
    cursor: pointer;
  }
  .notifications-container {
    display: inline-block;
    margin-left: 20px;
    .dropdown-menu {
      min-width: 600px;
      border: none;
      &:before {
        content: "";
        position: absolute;
        top: -12px;
        left: 10px;
        border-style: solid;
        border-width: 10px;
        border-color: transparent transparent #333;
      }
      .noti-head {
        background-color: #333;
        padding: 15px;
        color: #EEE;
        font-weight: bold;
        span {
          float: right;
        }
      }
      .noti-content {
        padding: 20px;
        ul {
          margin: 0;
          li {
            padding: 5px;
            font-weight: bold;
            color: #777;
            border-bottom: 1px solid #e9e8e8;
            margin-bottom: 10px;
            font-size: 15px;
            &:first-child {
              padding-top: 0;
            }
            &:last-child {
              padding-bottom: 0;
              margin-bottom: 0;
              border-bottom: none;
            }
            i {
              background-color: #EEE;
              padding: 10px;
              width: 30px;
              height: 30px;
              line-height: 11px;
              text-align: center;
              margin-right: 10px;
              position: relative;
              top: 2px;
              &.fa-dollar-sign {
                color: #3498db;
              }
              &.fa-envelope-open-text {
                color: #f1c40f;
              }
              &.fa-thumbs-up {
                color: #e74c3c;
              }
            }
            &:not(:first-child) i:before {
              position: relative;
              left: -5px;
            }
          }
        }
      }
      .noti-foot {
        a {
          display: block;
          padding: 10px;
          border-top: 3px solid #EEE;
          text-decoration: none;
        }
      }
    }
  }
}